import { Component, OnInit, Output, EventEmitter, Input, ElementRef, ViewChild } from '@angular/core';

@Component({
  selector: 'rosa-radio',
  templateUrl: './radio.component.pug',
  styleUrls: ['./radio.component.scss']
})
export class RadioComponent implements OnInit {
  // ----- Default ------- //
  @Input() public value: any;
  @Input() public name = '';
  @ViewChild('radio') radioControl: ElementRef;
   // ----- State ------- //
  @Input() public selected = false;
  // ----- Output ------- //
  @Output() ClickEvent: EventEmitter<any> = new EventEmitter<any>();
  // ----- Initial ------- //
  public index = `radio_${this.randomInteger(10, 1000)}`;
  constructor() { }

  ngOnInit() {
    const isChecked = this.selected && this.setChecked(this.radioControl.nativeElement);
  }
  public setChecked(radio: HTMLInputElement) {
    radio.checked = true;
    this.emitValue(this.value);
  }
  public emitValue(value): void {
    this.ClickEvent.emit(value);
  }
  public randomInteger(min, max) {
    let rand = min - 0.5 + Math.random() * (max - min + 1);
    rand = Math.round(rand);
    return rand;
  }

}
