import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { distinctUntilChanged } from 'rxjs/operators';
import { IndexedService } from '@app/bstorage/indexed.service';
import { FormService } from '../services/form.service';

@Component({
  selector: 'rosa-calculator',
  templateUrl: './calculator.component.pug',
  styleUrls: ['./calculator.component.scss']
})
export class CalculatorComponent implements OnInit, OnDestroy {
  public calculator: FormGroup;
  // setting ranges
  public ranges$: Observable<any[]>;
  public keys$: Observable<any[]>;
  public activeKey: any;
  public activeRange: any;
  public scores: any[];
  public buttonState = { text: `Сохранить рассчет`, class: `state_default`, disabled: false};
  public loadScore = false;
  public scoreResult$: Observable<any>;
  public scoreResult: any;
  public marginValue: number;
  public calculateHistory: any[] = [];
  public storageData$ = new Subject<any>();
  public order: any = 'date';
  public overdrafted = false;
  constructor(
    private _fb: FormBuilder,
    private _formService: FormService,
    private _indexedStore: IndexedService
  ) { }

  ngOnInit() {
    this.initCalculator();
    this.initForm();
    this.formChangeDetector();
    this.marginSubscribe();
    this.getSavedCalculates();
  }
  public initForm(): void {
    this.calculator = this._fb.group({
      type: [``, [Validators.required]],
      range: [``, [Validators.required]],
      segment: [``, [Validators.required]],
      collateral: [``, [Validators.required]],
      margin: ['', []],
      note: ['', []]
    });
  }
  public initCalculator() {
    this.ranges$ = this._formService.getRanges();
    this.keys$ = this._formService.getKeys();
  }
  public setType(type): void {
    this.calculator.controls['type'].setValue(type);
    console.log(this.calculator.controls['type'].value);
    if (this.calculator.controls['type'].value === 'overdraft') {
      this.overdrafted = true;
      console.log(this.overdrafted);
    } else {
      this.overdrafted = false;
      console.log(this.overdrafted);
    }
    this.calculate();
  }
  public setSegment(segment): void {
    this.calculator.controls['segment'].setValue(segment);
    this.calculate();
  }
  public setRange(range: string) {
    this.calculator.controls['range'].setValue(range);
    this.calculate();
  }
  public setKey(key: string) {
    this.calculator.controls['collateral'].setValue(key);
    this.calculate();
  }
  public formChangeDetector() {
    this.calculator.valueChanges.subscribe(
      val => {
       this.buttonState = { text: `Сохранить рассчет`, class: `state_default`, disabled: false};
    });
  }

  public marginSubscribe(): void {
    this.calculator
    .get('margin')
    .valueChanges
    .pipe(
        distinctUntilChanged(),
      ).subscribe(results => { this.marginValue = parseFloat(results.replace(/,/, '.')); });
  }

  public calculate(): boolean {
    if (!this.calculator.valid) { return false; }
    this.loadScore = true;
    setTimeout(() => {this.loadScore = false; }, 2300 );
    this.scoreResult$ = this._formService.calculate(this.calculator.value);
    this._formService.calculate(this.calculator.value).subscribe(
      score => this.scoreResult = score,
      data => { alert('Не удалось вычислить ставку'); }
    );
  }

  public saveCalculate(): void {
    const newCalculate = this.calculator.value;
    newCalculate.date = new Date();
    newCalculate.score = this.scoreResult.score;
    this.calculateHistory.push(newCalculate);
    this._indexedStore.saveToIndexed(`calculates`, JSON.stringify(this.calculateHistory)).then(value => {
      this.getSavedCalculates();
      this.storageData$.next(value);
      this.buttonState = { text: `Рассчет сохранен`, class: `state_success`, disabled: true};
    });
  }
  public getSavedCalculates() {
    // get saved calculates
    this._indexedStore.getFromIndexed(`calculates`).then(value => {
      if ( value === null ) { return; }
      value = JSON.parse(value);
      this.calculateHistory = value;
      this.storageData$.next(this.calculateHistory);
    });
  }
  ngOnDestroy(): void {
    this.storageData$.unsubscribe();
  }
  /*
  public sendScoreConfig(): void {
    // console.table(this.loanConfig);
    console.log(this.calculator.valid);
    if (!this.calculator.valid) { return; }
     this.loadScore = true;
     setTimeout(() => {this.loadScore = false; }, 2300 );

     this._formService.calculateScores(this.loanConfig)
     .subscribe(
       data => {
         this.loanConfig.result = data;
         this.loanConfig.date = new Date();
       },
       err => console.log(err)
     );
   }
   */
}
