import { Component, OnInit, Input, Output, EventEmitter, HostListener, ElementRef, OnChanges, SimpleChanges } from '@angular/core';

@Component({
  selector: 'rosa-dropdown-hoc',
  templateUrl: './dropdown-hoc.component.pug',
  styleUrls: ['./dropdown-hoc.component.scss']
})
export class DropdownHocComponent implements OnInit, OnChanges  {
  @Input() public values: any;
  @Input() public suffix = '';
  @Input() public prefix: string;
  @Input() public disabled = false;
  @Input() public forceSetValue = false;
  @Output() sendValue: EventEmitter<any> = new EventEmitter<any>();

  public isOpen = false;
  public selected: any;
  public previousSelected: any;
  public settedSuffix: string;

  constructor(private _elRef: ElementRef) { }

  ngOnInit() {
 //   this.previousSelected = this.selected;
    this.settedSuffix = this.suffix;
  }
  @HostListener('document:click', [`$event.target`])
  public onCLick(targetElement) {
    if (!this._elRef.nativeElement.contains(targetElement)) {
      this.isOpen = false;
    }
  }
  public ngOnChanges(changes: SimpleChanges) {
    if (changes['values']) {
      if ( changes['values'].currentValue ) {
        this.values = changes['values'].currentValue;
        this.selected = this.values[0];
        this.sendValue.emit(this.selected);
      }
    }
    if (changes['forceSetValue']) {
      if (changes['forceSetValue'].currentValue) {
        this.sendValue.emit(`овердрафт`);
        this.disabled = true;
      } else {
        this.disabled = false;
        this.sendValue.emit(this.selected);
      }
    }
  }
  public showDropdown() {
    this.isOpen = !this.isOpen;
  }
  public changeValue(value) {
    this.selected  = value;
    this.previousSelected = this.selected;
    this.sendValue.emit(value);
    this.showDropdown();
  }
  public closeAll(): void {
    console.log('im close');
    this.isOpen = false;
  }

}
