import { ButtonState } from './state.model';
import { Component, EventEmitter, OnInit, OnChanges, Output, Input, SimpleChanges } from '@angular/core';

@Component({
  selector: 'rosa-button',
  templateUrl: './button.component.pug',
  styleUrls: ['./button.component.scss']
})
export class ButtonComponent implements OnInit, OnChanges {
  // ----- Default ------- //
  @Input() public tabindex = -1;
  @Input() public value = 'Отправить';
  @Input() public type = 'button';
  @Input() public isDisabled = false;
  // ----- States ------- //
  @Input() public size = `size-default`;
  @Input() public stateClass = `state_default`;
  @Input() public state: ButtonState = { text: this.value, class: this.stateClass, disabled: false};
  // ----- Outputs ------- //
  @Output() ClickEvent: EventEmitter<any> = new EventEmitter<any>();


  constructor() { }

  ngOnInit() {
    if (this.state) {
      this.stateController(this.state);
    }
  }
  public ngOnChanges(changes: SimpleChanges) {
    if (changes['state']) {
      this.stateController(this.state);
    }
  }
  public stateController(state: ButtonState = {text: this.value, class: this.stateClass, disabled: false}) {
    const prevContent = this.value;
    const prevClass = this.stateClass;
    this.value = state.text;
    this.stateClass = state.class;
    this.isDisabled = state.disabled;
    if (!state.timer) { return; }
    setTimeout(() => {
      this.value = prevContent;
      this.stateClass = prevClass; },
      state.timer );
  }

}
