import { Component, OnInit, Input } from '@angular/core';
import { ClipboardService } from 'ngx-clipboard';
import { Observable } from 'rxjs';
export interface LoanScore {
  type: string;
  range: string;
  segment: string;
  collateral: string;
  score: number;
}
@Component({
  selector: 'rosa-score-result',
  templateUrl: './score-result.component.pug',
  styleUrls: ['./score-result.component.scss']
})
export class ScoreResultComponent implements OnInit {
  @Input() public score: Partial<LoanScore>;
  @Input() public margin: number;
  public tooltipText = `Скопировать результат`;
  public copied = false;
  constructor(
    private _clipboardService: ClipboardService,
  ) { }

  ngOnInit() {}

  public copy(): void {
    if (this.score.score === 0) { return; }
    this.tooltipText = `Скопировано`;
    this.copied = true;
    setTimeout( () => { this.tooltipText = `Скопировать результат`; this.copied = false; }, 3000 );
    this._clipboardService.copyFromContent(`${this.score.score}`);
  }
}
